.header {
  margin-top: .5rem;
  padding-left: 10px;

  ul {
    li {
      list-style: none;
      margin: 0;
      padding: .5rem 1rem;
      border-radius: 3px;

      &:hover {
        background-color: #444;

      }
    }
  }

  a {
    &:hover {
      text-decoration: none;
    }
  }

  h3 {
    a {
      color: white;
      &:hover {
        color: #00bc8c;
      }
    }
  }
}
