.random-planet {
  display: flex;
  padding: 1rem;
  border: 1px solid #444;
  min-width: min-content;

  .planet-image {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    margin-right: 1rem;
  }

  .list-group {
    margin: 1rem 0 0 1rem;

    &-item {
      padding: .25rem;

      .term {
        margin-right: .5rem;
      }
    }
  }
}